













body {
  margin: 0;
  padding: 0;
  border: 0;
}
.cell {
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 75px;
  border-bottom: 1px solid #aaa;
}
.cell-info {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}
.cell-info-head {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; /* 设置为IE盒模型 */
  width: 75px;
  height: 75px;
  padding: 10px;
}
.cell-info-detail {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  padding: 15px 0 15px 15px;
}
.cell-read {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 80px;
  background-color: #ccc;
}
.cell-delete {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 40px;
  background-color: red;
}
img {
  width: 55px;
  height: 55px;
  border-radius: 5px;
}
span {
  width: 0; /* 宽度为0 */
  font-size: 18px;
  white-space: nowrap;
  margin-left: 5%;/* 这里一定要用百分比，不然会撑出高度，无法完全缩放为0 */
}
